import { useFetcher, useLoaderData, useParams } from "@remix-run/react";
import { json, LoaderFunction } from "@remix-run/server-runtime";

export const loader: LoaderFunction = async ({ params }) => {
  const unsplashId = params.unsplashId;

  const resultatFraKonvertering = await fetch(`/routes/api/sqip/${unsplashId}`);
  return json({ result: [] }, 200);
};

export default function UnsplashUrl() {
  const url = useParams().unsplashId;
  const data = useLoaderData();
  console.log({ data });
  return <img className="h-1/3" src={url} alt="Bilde" />;
}
